/* Add font awesome rendering sharpness
*/

.fontawesome {
  display: inline-block;
  text-rendering: auto;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  transform: translate(0, 0);
}

/* truncate thing with attribute truncate="true" and other classes*/
[truncate=true] {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

[truncate="false"] {
  white-space: normal;
}

.handle-div {
  cursor: ew-resize;
}

[noselect='true'] {
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}


[not-selectable='true'] {
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

[user-select="all"] {
    -webkit-user-select: all;
    -khtml-user-select: all;
    -moz-user-select: all;
    -ms-user-select: all;
    user-select: all;
}

.radio-button-custom:checked + label + .radio-button-select .radio-button-circle,
.radio-button-custom:checked + label + * + .radio-button-select .radio-button-circle {
    opacity: 1;
}

textarea {
    outline: none;
}

@keyframes yellowHighlight {
  0% {
    background: #ffffdd;
  }
  100% {
    background: #fdfdfd;
  }
}

.yellow-highlight {
  animation: yellowHighlight 6s ease;
}

.is-grayed-out, .bem-button.is-with-spinner-shown {
  pointer-events: none;
  cursor: default;
}

.is-grayed-out[tooltipster] {
  pointer-events: auto;
}

.is-grayed-out[tooltipster]:active {
  pointer-events: none;
}

.is-with-spinner-shown:active {
  pointer-events: none;
}





